.products__title {
  font-family: @oswald;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
  margin: 0;
  margin-bottom: 13px;

  @media (min-width: @tablet) {
    margin: 0;
  }

  @media (min-width: @desktop) {
    margin: auto 0;
  }
}

.products__parameter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  margin-bottom: 15px;

  @media (min-width: @tablet) {
    margin-bottom: 25px;
  }

  @media (min-width: @desktop) {
    margin-bottom: 0;
    margin-right: 40px;
  }
}

.products__button-order {
  .button-order;

  @media (min-width: @tablet) {
    height: 40px;
    grid-column: 1/3;
  }

  @media (min-width: @desktop) {
    width: 244px;
    height: 40px;
    margin: auto 0;
    grid-column: 3;
  }
}
